<script>
import appConfig from '@src/app.config'
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../utils/Util';

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Otoritas Username',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Otoritas Username'+ ' ' +this.$route.params.action,
			api: Utils.Host(),
			message: "",
			data:[],
			dataprogdi:[],
			options: "",
			aksiEdit: false,
      		aksiSave: false,
			alert:false,
			dataotoritas: [],
			modelotoritas:"",
			datausername: [],
			modelusername:"",
			dataakses: [ { "f_kode": "1", "f_nama": "Tidak" }, { "f_kode": "0", "f_nama": "Aktif"} ], 
		}
	},
	mounted() {
		console.log("alamat", Utils.Host())
		this.Getdata()
		this.Getfilter()
		if (this.$route.params.action === 'tambah') {
		this.aksiEdit = true
		this.aksiSave = true
		}
		if (this.$route.params.action === 'edit') {
		this.aksiEdit = true
		this.aksiSave = true
		}
		if (this.$route.params.action === 'detail') {
		this.aksiEdit = false
		this.aksiSave = false
    }
  },
  computed: {
  },
  methods: {
    async Getdata() {
      try {
        const action = this.$route.params.action
        if (action === 'detail' || action === 'edit') {
          let response = await axios.get(this.api+"/otoritasusername/edit?f_password_id="+this.$route.params.id1 + '&f_otoritas_id='+this.$route.params.id2, Utils.authHeader());
          this.data = response.data;
		  let response1 = await axios.get(this.api+"/pegawailokasikerja/indexprogdi", Utils.authHeader());
		   // cek
		   for (const tampil of response1.data) {
		   await axios.get(this.api+"/pegawailokasikerja/index?f_progdi_id=" +
		   tampil.f_progdi_id+
				"&f_password_id=" +
				this.$route.params.id1, Utils.authHeader());
		   }
		  // cek
		  this.dataprogdi = response1.data;
		  this.data["f_password"] = "";
        }
      } catch (err) {
          console.error(err);
      }
    },
    async openEdit(id1, id2) {
        document.location = '/otoritasusernameaction/edit/' + id1 + '/' + id2
        // location.reload();
    },
    validation() {
      if (!this.data.modelotoritas) {
          return { code: 500, message: 'Otoritas Data ini harus diisi' }
      } else if (!this.data.modelusername) {
          return { code: 500, message: 'Otoritas Data ini harus diisi' }
      } else {
          return { code: 200, message: '' }
      }
    },
	async Getfilter() {
        try {
			let response = await axios.get(this.api+"/otoritas/index", Utils.authHeader());
    		this.dataotoritas = response.data;
			let response2 = await axios.get(this.api+"/username/index", Utils.authHeader());
    		this.datausername = response2.data;
        } catch (err) {
            console.error(err);
        }
      },
	  async DataUpdate(f_progdi_id, f_password_id, status){
			await axios.get(this.api+"/pegawailokasikerja/editupdate?f_progdi_id=" +
			f_progdi_id+
				"&f_password_id=" +
				f_password_id+
				"&status=" +
				status, Utils.authHeader());
				this.alert = true
        		this.message = 'Berhasil Update'

		},
		async DataUpdateall(f_password_id, status){
			await axios.get(this.api+"/pegawailokasikerja/editupdate?f_password_id=" +
				f_password_id+
				"&status=" +
				status, Utils.authHeader());
				this.alert = true
        		this.message = 'Berhasil Update'

		},
    async onSave() {
        const validation = this.validation()
        if (validation.code === 200) {
        var res = null
        const action = this.$route.params.action;
		const target = new URLSearchParams();
		target.append("f_otoritas_id", this.data.modelotoritas.f_otoritas_id);
		target.append("f_password_id", this.data.modelusername.f_password_id);
		target.append("f_otoritas_website", this.data.modelotoritas.f_otoritas_website);
		target.append("fn_otoritas_website", this.data.modelotoritas.fn_otoritas_website);

        if (action === 'tambah') {
            res = await axios.post(this.api+"/otoritasusername/add", target, Utils.authHeader());
        } else {
            res = await axios.put(this.api+"/otoritasusername/edit?f_otoritas_id="+ this.$route.params.id2+ '&f_password_id='+ this.$route.params.id1, target, Utils.authHeader());
        }
        if (res.data.code === 200) {
			this.alert = true
			this.message = res.data.message
            document.location = '/otoritasusernameaction/detail/' + res.data.data.f_password_id + '/' + res.data.data.f_otoritas_id;
            // this.Getdata();
            this.aksiEdit = !this.aksiEdit
            this.aksiSave = false
        }  else {
			this.alert = true
			this.message = res.data.message
        }
        } else {
			this.alert = true
			this.message = validation.message
        }
    },
  },
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									
									<b-alert
											v-model="alert"
											variant="danger"
											dismissible
											>{{ message }}</b-alert
										>

									<br>&nbsp;&nbsp;
									<br>
									<div class="table-responsive">
									
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Otoritas"
										label-for="input-horizontal"
									>
									<b-form-select v-model="data.modelotoritas">
										<option v-for='(value, key) in dataotoritas' :value='value'>{{value.f_otoritas}} - {{value.fn_otoritas_website}}</option>
									</b-form-select>
									</b-form-group>	


									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Username"
										label-for="input-horizontal"
									>
									<b-form-select v-model="data.modelusername">
										<option v-for='(value, key) in datausername' :value='value'>{{value.f_username}} - {{value.f_nama}}</option>
									</b-form-select>
									</b-form-group>	
										
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label-for="input-horizontal"
									>
									<b-button variant="success" @click="onSave(data.f_password_id, data.f_otoritas_id)"  v-if="aksiEdit">Simpan Data</b-button>
									&nbsp;
									<b-button variant="danger" @click="openEdit(data.f_password_id, data.f_otoritas_id)"  v-if="!aksiEdit">Edit Data</b-button>
									</b-form-group>	


									<br>
									<table class="table mb-0">
										<thead>
										<tr>
											<th scope="col">No</th>
											<th scope="col">Nama Jurusan</th>
											<th scope="col">Kode Progdi</th>
											<th scope="col">Nama Progdi</th>
											<th scope="col">Action
												<b-form-select>
													<option v-for='(value, key) in dataakses' :value='value.f_kode'  @click="DataUpdateall(data.f_password_id, value.f_kode)">{{value.f_nama}}</option>
												</b-form-select>
											</th>
										</tr>
										</thead>
										<tbody>
										<tr v-for="(tampil,index) in dataprogdi" :key="tampil.f_progdi_id">
											<th scope="row">{{index+1}}</th>
											<td>{{tampil.fn_jurusan}}</td>
											<td>{{tampil.f_kodeprogdi}}</td>
											<td>{{tampil.f_namaprogdi}}</td>
											<td>
												<b-form-select v-model="tampil.f_statusaktif">
													<option v-for='(value, key) in dataakses' :value='value.f_kode'  @click="DataUpdate(tampil.f_progdi_id, data.f_password_id, value.f_kode)">{{value.f_nama}}</option>
												</b-form-select>
											</td>
										</tr>
										</tbody>
									</table>								

									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
